      this.issendimg = !this.issendimg;
      this.showCustomPopup = !this.showCustomPopup;
    },
    upload(event) {
      let files = event.target.files[0];
      // console.log(files)
      this.fileName = this.getObjectUrl(files);
      //console.log(this.getObjectUrl(files))
      this.inputContent = `<img src="${this.fileName}" alt="">`;

      this.sendmsg();
      this.issendimg = false;
      this.showCustomPopup = false;

      var formData = new FormData();
      formData.append("user_id", this.$store.state.self.userId);
      formData.append("to_user_id", this.toUserId);
      formData.append("token", this.$store.state.self.token);
      formData.append("number", "1");
      formData.append("picture1", files);
      let config = {
        headers: {
          "Content-Type":
            "multipart/form-data;boundary = " + new Date().getTime()
        }
      };
      this.$axios
        .post("/picture", formData, config)
        .then(res => {
          //console.log(res.data);
          //this.$router.go("-1")
        })
        .catch(err => {
          //console.log(err.response.status)
          if (err.response.status == 500) {
            this.isload = true;
            this.iserr = true;
          }
        });
    },
    getObjectUrl(file) {
      let url = null;
      if (window.createObjectURL != undefined) {
        // basic
        url = window.createObjectURL(file);
      } else if (window.webkitURL != undefined) {
        // webkit or chrome
        url = window.webkitURL.createObjectURL(file);
      } else if (window.URL != undefined) {
        // mozilla(firefox)
        url = window.URL.createObjectURL(file);
      }
      return url;
    },

    question(item, index) {
      //onsole.log(item.self);
      //console.log(index);
      if (item.self) {
        console.log(this.$refs.share1[index].innerText);
        let length = this.$refs.share1[index].innerText.split("|").length;

        this.questionId = this.$refs.share1[index].innerText.split("|")[1];
        //console.log(this.questionId)
        let isnum = /^[0-9]+$/.test(this.questionId);
        if (isnum && length >= 2) {
          this.$router.push({ path: `/ShareTest/${this.questionId}` });
        }
      } else {
        let length = this.$refs.share1[index].innerText.split("|").length;
        this.questionId = this.$refs.share1[index].innerText.split("|")[1];
        //console.log(this.questionId)
        let isnum = /^[0-9]+$/.test(this.questionId);
        if (isnum && length >= 2) {
