      //this.oContent.scrollTop = this.oContent.scrollHeight;

      this.axios
        .post(
          "/chat",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            to_user_id: this.toUserId,
            content: this.inputContent,
            token: this.$store.state.self.token
          })
        )
        .then(res => {
          console.log(res.data);
          //{code: 404, msg: "请先添加好友", data: Array(0)}
          if (res.data.code == 404) {
            console.log("报404");
            // alert('jjhh')
            this.isverify = true;
            localStorage.setItem("isverify", this.isverify);
            //  this.isfault=true;
            // localStorage.setItem('isfault',this.isfault)
            this.$store.commit("changeVerify", this.isverify);
          }
          //console.log(this.inputContent)
        })
        .catch(err => {
          // console.log(err.response.status)
          if (err.response.status == 500) {
            this.isload = true;
            this.iserr = true;
          }
        })
        .then(res => {
          console.log(res == undefined);
          var date = new Date();
          var year = date.getFullYear();
          var month = date.getMonth() + 1;
          var day = date.getDate();
          var h = date.getHours(); //获取小时
          var m = date.getMinutes(); //获取分钟
          var s = date.getSeconds(); //获取秒
          if (month < 10) {
            month = "0" + month;
          }
          if (day < 10) {
            day = "0" + day;
          }
          this.nowDate =
            year + "-" + month + "-" + day + " " + h + ":" + m + ":" + s;
          console.log(this.nowDate);
          if (res == undefined) {
            console.log("200");
            this.axios
              .post("/past", {
                user_id: this.$store.state.self.userId,
                to_user_id: this.toUserId,
                start_time: "2019-1-24 16:22:43",
                end_time: this.nowDate,
                token: this.$store.state.self.token
              })
              .then(res => {
                this.isload = false; // this.islist = true
                console.log(res.data.data);
                this.listChat = res.data.data;
              })
              .catch(err => {
                console.log(err.response.status);
                if (err.response.status == 500) {
                  this.isload = true;
                  this.iserr = true;
                }
              });
          }
        });

      if (this.inputContent === "") {
        return;
      } else {
        this.msgs.push({
          userid: this.$store.state.self.userId,
          from_user_id: this.toUserId,
          //date: "2019-12-24",
          //loc: localStorage.addr,
          //from: this.fromName,
          content: this.inputContent,
          self: true
          //avatarUrl: this.avatarUrl
        });
        //聊天内容
        this.inputContent = "";
        setTimeout(() => {
          this.$refs.chattingContent.scrollTop = this.$refs.chattingContent.scrollHeight;
          console.log(this.$refs.chattingContent.scrollTop);
        }, 0);

        // setTimeout(
        //   () => (this.oContent.scrollTop = this.oContent.scrollHeight),
        //   0
        // );
      }
    },
